<template>
  <div class="show_article">
    <v-md-preview :text="'### ' + article.article_title + article.article_content"></v-md-preview>
  </div>
  <div class="article_other">
    <div style="width: 120px;margin: 0 auto">
      <div class="article_other_img" @click="add_hits"></div>
      <div class="article_other_text">{{ article.article_hits }}</div>
    </div>
  </div>
  <div class="comment" v-for="(comment, i) in comment_list" :key="comment">
    <div class="comment_main">
      <div class="comment_info_box">
        <img :src="comment.temporaryMotto_img" alt="" class="comment_author">
        <div class="comment_info">
          <p class="comment_name">{{ comment.temporaryMotto_name }}</p>
          <p class="comment_time">{{ comment.comment_time }}</p>
        </div>
        <div class="comment_reply" @click="is_reply(comment.id, $event, i)"><span>回复</span>
        </div>
      </div>
      <div class="comment_information">{{ comment.comment_content }}</div>
    </div>
    <div class="comment_son" v-for="(secondary) in comment.comment_secondary" :key="secondary">
      <div class="comment_info_box_son">
        <img :src="secondary.temporaryMotto_img" alt="" class="comment_author">
        <div class="comment_info">
          <p class="comment_name">{{ secondary.temporaryMotto_name }}</p>
          <p class="comment_time">{{ secondary.comment_time }}</p>
        </div>
        <div class="comment_reply" @click="is_reply(comment.id, $event, i)">回复</div>
      </div>
      <div class="comment_information_son">{{ secondary.comment_content }}</div>
    </div>
  </div>
  <div class="reply_box" v-bind:style="reply_box_sty" v-if="is_show_reply_box">
    <span class="hair_comment">发表评论</span>
    <el-input class="input_info" v-model="comment_name" placeholder="名称*" style="margin-right: 10%"
              maxlength="16"/>
    <el-input class="input_info" v-model="comment_email" placeholder="邮箱*" maxlength="20"/>
    <el-input v-model="comment_content" :autosize="{ minRows: 2, maxRows: 4 }" type="textarea"
              placeholder="期待大佬的精彩发言"
              style="margin: 10px 0;">
    </el-input>
    <el-button type="primary" @click="is_show_reply_box=false">关闭</el-button>
    <el-button type="primary" style="float: right" @click="publish(2)">发表</el-button>
  </div>
  <div class="reply_box_fixed">
    <div class="box_fixed" v-if="!is_show_reply_box">
      <span class="hair_comment">发表评论</span>
      <el-input class="input_info" v-model="comment_name" placeholder="名称*" style="margin-right: 10%;" maxlength="16"/>
      <el-input class="input_info" v-model="comment_email" placeholder="邮箱*" maxlength="20"/>
      <el-input v-model="comment_content" :autosize="{ minRows: 5, maxRows: 10 }" type="textarea"
                placeholder="期待大佬的精彩发言" style="margin: 20px 0">
      </el-input>
      <div class="publish_box">
        <el-button type="primary" style="float: right" @click="publish(1)">发表</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import VMdPreview from '@kangc/v-md-editor/lib/preview'
import '@kangc/v-md-editor/lib/style/preview.css'
import githubTheme from '@kangc/v-md-editor/lib/theme/github'
import '@kangc/v-md-editor/lib/theme/style/github.css'
import hljs from 'highlight.js'
import { ElMessage } from 'element-plus'

VMdPreview.use(githubTheme, {
  Hljs: hljs
})
export default {
  name: 'content',
  data () {
    return {
      article: {},
      comment_list: [],
      comment_list_new: [],
      comment_name: '',
      comment_email: '',
      comment_content: '',
      reply_box_sty: {
        top: '200px'
      },
      is_show_reply_box: false,
      temporary_id: ''
    }
  },
  components: {
    VMdPreview
  },
  methods: {
    add_hits () {
      this.api.initial.add_hits(localStorage.ip, this.article.id).then(res => {
        if (res.data.message === '已经点过赞了') {
          ElMessage({
            message: res.data.message,
            duration: 1000
          })
        } else {
          ElMessage.success({
            message: res.data.message,
            type: 'success',
            duration: 1000
          })
          this.article.article_hits++
        }
      })
    },
    publish (type) {
      if (type === 1) {
        this.api.initial.new_comment(
          type, this.comment_email, localStorage.ip,
          this.comment_name, this.article.id,
          this.comment_content, 0
        ).then(res => {
          this.comment_list.unshift(res.data.data)
        })
      } else {
        this.api.initial.new_comment(
          type, this.comment_email, localStorage.ip,
          this.comment_name, this.article.id,
          this.comment_content, this.temporary_id
        ).then(res => {
          this.comment_list[this.index].comment_secondary.push(res.data.data)
          this.is_show_reply_box = false
        })
      }
      localStorage.setItem('comment_name', this.comment_name)
      localStorage.setItem('comment_email', this.comment_email)
    },
    is_reply (commentId, event, i) {
      this.reply_box_sty.top = event.pageY + 'px'
      this.is_show_reply_box = true
      this.temporary_id = commentId
      this.index = i
    },
    initial () {
      this.api.initial.get_id_article(this.$route.query.id).then(res => {
        this.article = res.data.data
        this.api.initial.comment_info(this.article.id).then(res => {
          this.comment_list = res.data.data
        })
      })

      this.comment_name = localStorage.comment_name
      this.comment_email = localStorage.comment_email
    }
  },
  created () {
    this.initial()
  }
}
</script>

